import './App.css';
import React, { useState,useEffect } from 'react';

const API = 'https://api.scryfall.com/cards/search?q=lang:ja+name='

function App(){
  const[searchWord, setSearchWord] = useState('');
  const[result, setResult] = useState([]);
  const[timerId, setTimerId] = useState(0);

  useEffect(() => {
    const timerId = setTimeout(() => {
      fetch(API + searchWord)
      .then(res => res.json())
      .then(cardInfo => {setResult(cardInfo.data)})
    }, 500);
    setTimerId(timerId)
  },[searchWord])

  function handleChange(e){
    setSearchWord(e.target.value)
    clearTimeout(timerId)
  }

  let list = !(result instanceof Array) ? [] : result.map(cardInfo => ShowCardInfo(cardInfo))
  return (
    <div>
      カード名　<input type="search" value={searchWord} onChange={handleChange} />
      <ul>
        {list}
      </ul>
    </div>
  );
}

function ShowCardInfo(cardInfo){
  return(
    cardInfo.image_uris ? <li key={cardInfo.id}><img src={cardInfo.image_uris.small}></img></li> : <li></li> 
  );
}

export default App;
